.slick-slider {
  position: relative;
  display: block;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -ms-touch-action: pan-y;
  touch-action: pan-y;
  -webkit-tap-highlight-color: transparent;
}

.slick-list {
  position: relative;
  overflow: hidden;
  display: block;
  margin: 0;
  padding: 0;
}

.slick-list:focus {
  outline: none;
}

.slick-loading .slick-list {
  background: white;
}

.slick-list.dragging {
  cursor: pointer;
  cursor: hand;
}

.slick-slider .slick-list,
.slick-track,
.slick-slide {
  transform: translate3d(0, 0, 0);
}

.slick-slide img {
  transform: translate3d(-50%, -50%, 0);
}

.slick-track {
  position: relative;
  left: 0;
  top: 0;
  display: block;
  zoom: 1;
}

.slick-track:before, .slick-track:after {
  content: "";
  display: table;
}

.slick-track:after {
  clear: both;
}

.slick-loading .slick-track {
  visibility: hidden;
}

.slick-slide {
  float: left;
  height: 100%;
  min-height: 1px;
}

.slick-slide img {
  display: block;
}

.slick-slide.slick-loading img {
  display: none;
}

.slick-slide.dragging img {
  pointer-events: none;
}

.slick-initialized .slick-slide {
  display: block;
}

.slick-loading .slick-slide {
  visibility: hidden;
}

.slick-vertical .slick-slide {
  display: block;
  height: auto;
  border: 1px solid transparent;
}

.slick-prev,
.slick-next {
  position: absolute;
  bottom: 40px;
  font: 0/0 a;
  text-shadow: none;
  color: transparent;
  margin-top: -13px;
  height: 26px;
  width: 26px;
  background: none;
  right: calc(7% + 30px);
  z-index: 1;
  background: transparent;
  border: none;
  transition: 0.4s;
}

.slick-prev:hover,
.slick-next:hover {
  background-color: transparent;
}

.slick-prev:hover:before,
.slick-next:hover:before {
  transform-origin: 50% 50%;
  transform: translate(-50%, -50%) rotate(180deg);
}

.slick-prev:focus,
.slick-next:focus {
  outline: none;
}

.slick-prev.slick-disabled,
.slick-next.slick-disabled {
  opacity: 0.2;
  cursor: default;
}

.slick-prev:before,
.slick-next:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(180deg);
  transform-origin: 50% 50%;
}

.slick-next {
  right: 7%;
}

.slick-next:before {
  content: "";
  display: inline-block;
  transform: translate(-50%, -50%) rotate(0deg);
}

.slick-next:hover:before {
  transform: translate(-50%, -50%) rotate(0deg);
}

.slick-dots {
  position: absolute;
  margin: 0;
  padding: 0;
  font-size: 0;
  bottom: 8px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 10;
